package model

object RafmanTranslator {
    fun translateJapaneseToRafman(text: String): String {
        return buildString {
            text.forEach {
                append(japaneseToRafmanMap["$it"] ?: "$it")
            }
        }
    }

    fun translateRafmanToJapanese(text: String): String {
        return buildString {
            text.forEach {
                append(rafmanToJapaneseMap["$it"] ?: "$it")
            }
        }
    }

    private val japaneseToRafmanMap = mapOf(
        "" to "3", "" to "e", "" to "4", "" to "5", "" to "6",
        "" to "t", "" to "g", "" to "h", "" to ":", "" to "b",
        "" to "x", "" to "d", "" to "r", "" to "p", "" to "c",
        "" to "q", "" to "a", "" to "z", "" to "w", "" to "s",
        "" to "u", "" to "i", "" to "1", "" to "<", "" to "k",
        "" to "f", "" to "v", "" to "2", "" to "^", "" to "-",
        "" to "j", "" to "n", "" to "]", "" to "/", "" to "m",
        "" to "7", "" to "8", "" to "9",
        "" to "o", "" to "l", "" to ".", "" to ";", "" to "\\",
        "" to "0", "" to "=", "" to "y",
        "" to "t@", "" to "g@", "" to "h@", "" to ":@", "" to "b@",
        "" to "x@", "" to "d@", "" to "r@", "" to "p@", "" to "c@",
        "" to "q@", "" to "a@", "" to "z@", "" to "w@", "" to "s@",
        "" to "f@", "" to "v@", "" to "2@", "" to "^@", "" to "-@",
        "" to "f[", "" to "v[", "" to "2[", "" to "^[", "" to "-[",
        "" to "3", "" to "e", "" to "4", "" to "5", "" to "6",
        "" to "z",
        "" to "7", "" to "8", "" to "9",
        "" to "3", "" to "e", "" to "4", "" to "5", "" to "6",
        "" to "t", "" to "g", "" to "h", "" to ":", "" to "b",
        "" to "x", "" to "d", "" to "r", "" to "p", "" to "c",
        "" to "q", "" to "a", "" to "z", "" to "w", "" to "s",
        "" to "u", "" to "i", "" to "1", "" to "<", "" to "k",
        "" to "f", "" to "v", "" to "2", "" to "^", "" to "-",
        "" to "j", "" to "n", "" to "]", "" to "/", "" to "m",
        "" to "7", "" to "8", "" to "9",
        "" to "o", "" to "l", "" to ".", "" to ";", "" to "\\",
        "" to "0", "" to "=", "" to "y",
        "" to "t@", "" to "g@", "" to "h@", "" to ":@", "" to "b@",
        "" to "x@", "" to "d@", "" to "r@", "" to "p@", "" to "c@",
        "" to "q@", "" to "a@", "" to "z@", "" to "w@", "" to "s@",
        "" to "f@", "" to "v@", "" to "2@", "" to "^@", "" to "-@",
        "" to "f[", "" to "v[", "" to "2[", "" to "^[", "" to "-[",
        "" to "3", "" to "e", "" to "4", "" to "5", "" to "6",
        "" to "z",
        "" to "7", "" to "8", "" to "9",
        "" to "@", "" to "["
    )

    private val rafmanToJapaneseMap = mapOf(
        "3" to "", "e" to "", "4" to "", "5" to "", "6" to "",
        "t" to "", "g" to "", "h" to "", ":" to "", "b" to "",
        "x" to "", "d" to "", "r" to "", "p" to "", "c" to "",
        "q" to "", "a" to "", "z" to "", "w" to "", "s" to "",
        "u" to "", "i" to "", "1" to "", "<" to "", "k" to "",
        "f" to "", "v" to "", "2" to "", "^" to "", "-" to "",
        "j" to "", "n" to "", "]" to "", "/" to "", "m" to "",
        "7" to "", "8" to "", "9" to "",
        "o" to "", "l" to "", "." to "", ";" to "", "\\" to "",
        "0" to "", "=" to "", "y" to "",
        "@" to "", "[" to ""
    )
}