package component

import model.RafmanTranslator
import react.RBuilder
import react.dom.div
import react.dom.h1
import react.dom.header

fun RBuilder.app() {
    header {
        h1 {
            + ""
        }
    }
    div(classes = "main") {
        translator(
            "",
            "",
            RafmanTranslator::translateRafmanToJapanese
        )
        translator(
            "",
            "",
            RafmanTranslator::translateJapaneseToRafman
        )
    }
}